/* ======= Object-sans Regular ======*/
@font-face {
    font-family: 'object sans regular';
    src: url("../../Fonts/object-sans/ObjectSans-Regular.eot");
    src: url("../../Fonts/object-sans/ObjectSans-Regular.woff") format('woff'),
        url("../../Fonts/object-sans/ObjectSans-Regular.woff2") format('woff2'),
        url("../../Fonts/object-sans/ObjectSans-Regular.otf") format('opentypefile'),
        url("../../Fonts/object-sans/ObjectSans-Regular.ttf") format('truetype'),
        url("../../Fonts/object-sans/ObjectSans-Regular.svg#ObjectSans-Regular.svg") format('svg');
    font-weight: 400 !important;
    font-style:normal !important; 
    /* is there need to define font-style:normal here? bcaz already define inside the body*/
}

